.spiner-wraper-on {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  transition: all 2s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spiner-visible {
  height: 50px;
  width: 50px;
  border: 3px solid #ff6b08;
  border-top-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  transition: all 2s;
  animation: spinner 1s linear infinite;
}

@keyframes spinner {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

